import * as React from 'react';
import { RenderNodeProps, Controller, Element, Block } from '@ali/4ever-cangjie';
import { Card } from '@ali/4ever-plugin-card';
import{ Placeholder } from '@ali/4ever-component';
import { removeCard } from './actions';

const renderUnknownCard = {
  [Card.TYPE]: (props: RenderNodeProps, controller: Controller) => {
    if (!Block.isBlock(props.node)) {
      return <></>;
    }
    return (
      <Placeholder
        {...props}
        editor={controller}
        handleRemoveNode={() => controller.run('onAction', removeCard(props.node as Element))}
      />
    );
  },
};

export default renderUnknownCard;

